@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.analitics {
  &-page {
    padding-bottom: 80px;

    @include mixin.tablet {
      padding-bottom: 64px;
    }

    &__title {
      @include mixin.tablet {
        margin-bottom: 16px;
      }
    }
  }
}

.analitics-tabs {
  margin-bottom: 70px;

  @include mixin.tablet {
    margin-bottom: 0;
  }

  &__heads {
    margin-bottom: 48px;
    display: flex;
    align-items: center;
    gap: 48px;
    overflow-x: auto;
    scrollbar-width: none;

    @include mixin.tablet {
      margin-bottom: 32px;
    }

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__head {
    color: #B1B5BD;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-style: normal;
    font-weight: 700;
    line-height: 1.33;
    background: transparent;
    cursor: pointer;
    white-space: nowrap;

    &--active {
      color: var.$secondary;
    }
  }

  &__bodies {
    width: 100%;
  }
}

.analitics-tab {
  display: none;

  &--active {
    display: block;
  }
}

.dashboard-container {
  margin-bottom: 64px;

  @include mixin.small-tablet {
    margin-bottom: 32.63px;
    padding: 0;
  }

  .banner-graph {
    padding-right: 60px;
  }
}

.dashboard {
  display: grid;
  padding: 48px 48px 64px;
  gap: 48px;

  @include mixin.small-tablet {
    border-radius: 0;
    padding: 32px 17px;
  }

  .row {
    gap: 15px 0;
  }

  &:hover {
    background: var.$secondary;
    color: var.$light;
  }

  &__graph-title {
    margin-bottom: 38px;
    color: var.$light;

    @include mixin.small-tablet {
      margin-bottom: 24px;
    }
  }

  &__graph-desc {
    color: rgba(var.$light, 0.50);
    font-family: var.$font-family;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 300;
    line-height: 1.4;
  }

  &__stats {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 0px 15px;
    padding-top: 15px;

    @include mixin.small-tablet {
      grid-template-columns: repeat(1, 1fr);
      gap: 0px 15px;
    }
  }

  &--single &__stats {
    grid-template-columns: repeat(1, 1fr);
  }

  &__stat-title {
    margin-top: 15px;
    color: var.$light;
    display: flex;
    justify-content: space-between;
  }

  &__stat-percent {
    margin-bottom: 20px;
    color: var.$light;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-style: normal;
    font-weight: 700;
    line-height: 1.1;

    @include mixin.small-tablet {
      font-size: 3.2rem;
      line-height: 1.25;
      margin-bottom: 20px;
    }
  }

  &__stat:last-child &__stat-percent {
    margin-bottom: 0;
  }

  &__stat-text {
    color: rgba(var.$light, 0.50);
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 300;
    line-height: 1.428;
    margin-bottom: 8px;

    @include mixin.small-tablet {
      font-size: 1.2rem;
      line-height: 1.428;
    }
  }

  &__stat-dir {
    color: var.$light;
    font-family: var.$font-family;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.428;

    &--col {
      flex-direction: column;
    }

    & > div {
      display: flex;
      justify-content: space-between;
      width: 50%;
    }

    &--long > div {
      width: 80%;
    }

    &--row {
      display: flex;
      gap: 10px;

      div {
        display: flex;
        gap: 2px;
        flex-direction: column;
      }
    }
  }

  &__download {
    margin-top: 12px;
    padding: 8px 16px;
    float: right;
    font-weight: 400;

    &:hover {
      color: #fff;
    }

    svg {
      margin-left: 5px;
      path {
        fill: #fff;
      }
    }
  }

  &-row {
    display: flex;
    flex-wrap: wrap;
    gap: 8px 27px;
    margin-bottom: 18px;
    color: #fff;

    &--wrap {
      flex-wrap: wrap;
      gap: 0;
    }

    &__title {
      font-size: 18px;
      font-weight: 500;
      line-height: 1.66;
    }

    &__count {
      font-size: 40px;
      font-weight: 700;
      line-height: 1.1;
      margin-bottom: 3px;
    }

    &__percent {
      display: flex;
      gap: 10px;

      span {
        font-size: 14px;
        font-weight: 400;
        line-height: 1.43;
      }
    }

    &__right {
      display: flex;
      gap: 15px;
    }

    &__right-title {
      font-size: 18px;
      font-weight: 300;
      line-height: 1.11;
      color: rgba(255, 255, 255, 0.5);
    }

    &__right-text {
      font-size: 18px;
      font-weight: 400;
      line-height: 1.11;
    }

    &__main-title {
      font-size: 18px;
      font-weight: 500;
      line-height: 1.66;
      width: 100%;
    }

    &__main-count {
      font-size: 40px;
      font-weight: 700;
      line-height: 1.1;
    }
  }
}

.analitics-load {
  margin-bottom: 64px;
  padding: 24px 32px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  gap: 8px 32px;

  @include mixin.small-tablet {
    padding: 0;
    border: none;
    margin-bottom: 32px;
  }

  &__counts {
    flex-grow: 1;
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-style: normal;
    font-weight: 500;
    line-height: 1.33;

    @include mixin.small-tablet {
      font-size: 1.8rem;
      line-height: 1.66;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 12px;

    @include mixin.small-tablet {
      gap: 8px;
    }
  }

  &__download {
    padding: 8px 16px;
    display: flex;
    align-items: center;
    gap: 4px;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.5px;
    color: var.$gray2;

    &:hover {
      color: var.$gray2;
    }

    @include mixin.small-tablet {
      padding: 8px 12px;
    }

    svg {
      width: 20px;
      height: 20px;
    }
  }
}

.analitics-filter {

  &__dropdown-btn {
    padding: 8px 16px;
    display: flex;
    align-items: center;
    gap: 4px;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.5px;
    color: var.$gray2;

    @include mixin.small-tablet {
      padding: 8px 12px;
    }

    svg {
      width: 16px;
      height: 16px;
      fill: var.$gray2;
    }
  }
}

.analitics {
  margin-bottom: 128px;

  @include mixin.small-tablet {
    margin-bottom: 64px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 48px 0px;
    margin-bottom: 64px;
    margin-left: -24px;
    margin-right: -24px;

    @include mixin.small-tablet {
      gap: 32px 0px;
      margin-bottom: 32px;
      margin-left: unset;
      margin-right: unset;
    }

    .col-12,
    .col-lg-6 {
      padding: 0 24px;

      @include mixin.small-tablet {
        padding: unset;
      }
    }
  }

  &__item {
    width: 100%;
    min-height: 100%;
    box-sizing: border-box;
    padding: 16px 24px;

    .highcharts-scrolling {
      flex-wrap: nowrap;
      overflow-y: hidden;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }

  &__header {
    margin-bottom: 8px;
  }

  &__item-title {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 15px;
    text-align: left;
    white-space: normal;
    margin-bottom: 8px;
  }

  &__info {
    position: relative;
    display: inline-block;
    flex-shrink: 0;
    align-self: flex-start;
  }

  &__info-btn {
    padding: 0;
    margin: 0;
    border: none;
    padding: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: transparent;
    background-image: url('@images/icons/icon_info.svg');
    background-size: contain;
    background-position: center;
    cursor: pointer;
  }

  &__info-body {
    position: absolute;
    top: 120%;
    right: 120%;
    min-width: 180px;
    max-width: 300px;
    padding: 8px 10px;
    box-sizing: border-box;
    border: 1px solid #c0c0c0;
    box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.08);
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.8);
    z-index: 10;
    font-size: 12px;
    font-weight: 400;
    line-height: 1.2;
    color: #000;
    opacity: 0;
    pointer-events: none;
    transition: 0.3s linear;
  }

  &__info:hover &__info-body {
    opacity: 1;
  }

  &__filters {
    display: flex;
    justify-content: flex-start;
    gap: 15px;
    align-items: stretch;
    flex-wrap: wrap;
    margin-top: 15px;

    .badge--empty {
      display: flex;
      align-items: center;
      gap: 7.33px;
      padding: 8px 16px;
      font-size: 1.6rem;
      font-weight: 400;
      line-height: 1.5;
      color: var.$gray2;
    }
  }

  &__more {
    display: block;
    padding: 12px 16px;
    text-align: center;
    margin: 0 auto;
    width: 100%;
    max-width: 305px;
    box-sizing: border-box;
    cursor: pointer;

    @include mixin.phone {
      max-width: unset;
    }
  }
}

.report {
  &__filter-results {
    text-align: center;
    font-size: 1.4rem;
    font-weight: 700;
    line-height: 1.5;
    color: var.$secondary;
    margin-bottom: 2px;
    display: flex;
    justify-content: center;
    gap: 35px;
  }
}

.publishes {
  &__title {
    margin-bottom: 48px;

    @include mixin.tablet {
      margin-bottom: 32px;
    }
  }

  &__list {
    gap: 32px 0;
  }
}

.publish-item {
  height: 100%;
  box-sizing: border-box;
  padding: 32px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  transition: 0.3s linear;

  &:hover {
    background: var.$secondary;
  }

  &__head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__type &__tag {
    padding: 2px 12px;
    font-family: var.$font-family2;
    font-size: 1.4rem;
    font-weight: 500;
    line-height: 1.428;
  }

  &__date {
    font-size: 1.8rem;
    font-weight: 300;
    line-height: 1.66;
    color: var.$gray;
    margin-bottom: 16px;
  }

  &__title {
    flex-grow: 1;
    margin-bottom: 23px;
    line-height: 1.66;
    max-width: 95%;
    transition: 0.3s linear;
  }

  &:hover &__title {
    color: var.$light;
  }

  &__more {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 6px;
    font-size: 1.8rem;
    font-weight: 500;
    line-height: 1;
    color: var.$secondary;
    transition: 0.3s linear;

    svg {
      width: 15px;
    }

    path {
      transition: 0.3s linear;
    }
  }

  &:hover &__more {
    color: var.$light;

    path {
      stroke: var.$light;
    }
  }
}